/* side nav collapse threshold */



@media (max-width: 767px)
{
  /* since 767px is default for bootstrap 3.3.7, we need this other than 767px */
  

  /* enable top nav bar */
  .top-nav {
    display: block;
  }

  /* disable side nav */
  :root {
    --side-nav-width: 0px;
    --top-nav-height: var(--top-nav-height-def);
  }

  /* disable side nav */
  #side-nav-container {
    left: calc(-1 * var(--side-nav-width-def));
  }


  .upside-down-tabs {
    left: 0px;
    width: 100%;
  }



  .searchbox-container {
    /* fixed to top nav */
    position: absolute;
    top: 10px;
    left: unset;
    right: var(--main-container-margin);
  }

  /* right to left opening */
  #search-box {
    --searchbox-expand-width: calc(100vw - (var(--main-container-margin) * 2));
  }

  #search-results {
    max-width: calc(100vw - (var(--side-nav-width) + (var(--main-container-margin) * 2) + var(--search-results-margin-left)));
  }



  .color_scheme_switch_top_holder {
    /* fixed to top nav */
    position: absolute;
    display: inline;
    top: 14px;
    right: calc(var(--main-container-margin) + 60px);
  }





  #toc-container {
    display: none;
  }


}
